import React from 'react'

import PageTitle from '../components/PageTitle/pageTitle'

export default () => (
  <div className="center mw8 mt4 pv4 ph3">
		<PageTitle title="contact" align="left" />
		<p className="f4 lh-copy black-70 fw1">
			If you are interested in working with me, I would love to talk. Please
			write to me at the email address below:{' '}
		</p>
		<a href="mailto:kofosu89@gmail.com" className="f4 fw1 link">
			kofosu89@gmail.com
		</a>
    </div>
)



